@import "../variables.less";
@import "../mixins.less";
@import "lht-variables.less";

.separator {
  margin-top: 1.4em;
  margin-bottom: 1.4em;
  border-bottom: 1px dashed @lht-medium-gray-c;
}

.makeSpacerClasses(@max, @unit: px, @i:5) when (@i <= @max) {
  div.space-@{i} {
    height: unit(@i, @unit);
  }
  .makeSpacerClasses(@max,  @i:(@i+5));
}
.makeSpacerClasses(50);
