//
// Pagination (multiple pages)
// --------------------------------------------------
.pagination {
  > li {
    > a,
    > span {
      // some more space to match button size
      padding: @padding-large-vertical @padding-large-horizontal;
    }
  }
  > .active > a,
  > .active > span {
    &,
    &:hover,
    &:focus {
      z-index: 4; // don't let others override active blue borders
    }
  }

}

// Our (!) Sizing
// --------------------------------------------------

// Large
.pagination-lg {
  .pagination-size(@padding-large-vertical; @padding-large-horizontal; @font-size-large; @line-height-large; @border-radius-large);
}

// Small
.pagination-sm {
  .pagination-size(@padding-medium-vertical; @padding-medium-horizontal; @font-size-small; @line-height-small; @border-radius-small);
}
