@import "../variables.less";
@import "../mixins.less";
@import "lht-variables.less";

// use as mixin:
@import "lht-badges.less";

//
// Panels
// --------------------------------------------------


.panel-title {
  // make everything like h4...
  font-size: @font-size-h4;
  color: @lht-heading-color;
  // ... and add 1px for same height as regular headings
  margin-top: 1px;
  display: inline-block;
}

.panel-footer {
  // Same gradient as menu bar / panel heading
  #gradient > .vertical (@start-color: @lht-menubar-gradient-top; @end-color: @lht-menubar-gradient-bottom);
  border: none;
}


// Contextual variations
.panel-default {
  .panel-variant(@panel-default-border; @panel-default-text; @panel-default-heading-bg; @panel-default-border);

  & > .panel-heading {
    // Same gradient as menu bar
    #gradient > .vertical (@start-color: @lht-menubar-gradient-top; @end-color: @lht-menubar-gradient-bottom);
    border: none;

    .badge {
      .badge(); // mixin!
    }
  }
}

.panel-primary {
  .panel-variant(@panel-primary-border; @panel-primary-text; @panel-primary-heading-bg; @panel-primary-border);

  // dirty hack: remove the outer border and have
  // separate borders for heading and body

  border: 0px transparent;

  & > .panel-heading {
    background-image: none;
    border: 1px solid @panel-primary-border;
    border-bottom: none;
    & + .panel-body {
      border-top: none;
      border-top-left-radius: 0;
      border-top-right-radius: 0;
    }

    .badge-disabled,
    .badge.disabled {
      //color: @lht-btn-primary-disabled-bg-color;
      background-color: @lht-badge-bg-primary-disabled
    }

    a.badge {
      &:hover,
      &:focus {
        .lht-active-badge-link-hover; //same as "active": negative badge on navy-blue background
      }
    }

  }

  & > .panel-body {
    border: 1px solid @panel-default-border;
    border-radius: @panel-border-radius;
  }

  & .panel-title {
    color: @panel-primary-text;
  }

  & .panel-footer {
    // Deprecated: we don't allow for a footer in a primary panel!
    display: none !important;
  }

}


// revert unsupported styles to their original looks
.panel-title {
  .panel-success & {
    color: @panel-success-text;
  }
  .panel-warning & {
    color: @panel-warning-text;
  }
  .panel-danger & {
    color: @panel-danger-text;
  }
}



