@import "../variables.less";
@import "../mixins.less";
@import "lht-variables.less";

//
// Modals
// --------------------------------------------------

// .modal-open      - body class for killing the scroll
// .modal           - container to scroll within
// .modal-dialog    - positioning shell for the actual modal
// .modal-content   - actual modal w/ bg and corners and shit


// Modal header (Top section with title and dismiss)
.modal-header {
  color: @lht-modal-title-color;
  background-color: @lht-modal-heading-bg;
  #gradient > .vertical(@start-color: @lht-btn-primary-gradient-top; @end-color: @lht-btn-primary-gradient-bottom);
  .reset-filter(); // Disable gradients for IE9 because filter bleeds through rounded corners; see https://github.com/twbs/bootstrap/issues/10620
  border-top-left-radius: (@border-radius-large - 1px); // use bleeding to avoid halo effects
  border-top-right-radius: (@border-radius-large - 1px);
  border-bottom: none;
  padding: @lht-modal-title-padding;
  min-height: @lht-modal-heading-min-height;
}

// Close icon
.modal-header .close {
  margin-top: -3px;
  color: @lht-modal-title-color;
  .opacity(1);

  &:hover,
  &:focus {
    color: @lht-toolbar-link-primary-disabled-color;
    text-shadow: 0 1px 0 @lht-toolbar-link-primary-disabled-color;
  }

}

// Title text within header
.modal-title {
  font-size: @font-size-h4;
  font-weight: @headings-font-weight;
  color: @lht-modal-title-color;
  margin-top: 1px;
}

// Deprecated: we don't allow for footers
.modal-footer {
  display: none !important;
}
