// LHT colors
// ------------------------------------------------

// Primary colors
@lht-yellow:    #ffb300; // Header, traffic light status
@lht-navy-blue: #000066; // Link highlights, Headlines, Default button indication

// Gradient colors
@lht-yellow-lighter:    #ffc02d;
@lht-navy-blue-lighter:	#35357e;
@lht-navy-blue-darker:	#101059;

// experiemental (for links)
@lht-navy-blue-even-lighter: #2728ba;


// Grayscale
@lht-dark-gray-a:   #333333; // Text
@lht-dark-gray-b:   #555555; // Menu flyout links
@lht-medium-gray-a: #777777; // Links, image labels
@lht-medium-gray-b: #999999; // Page sub-header
@lht-medium-gray-c: #BBBBBB; // NEW: for disabled elements
@lht-light-gray-a:  #CCCCCC; // Dotted lines
@lht-light-gray-b:  #DDDDDD; // Hover (buttons, menues, etc.)
@lht-light-gray-c:  #F5F5F5; // Teaser background, flyout background, Alternate table rows


// Secondary colors
@lht-dark-red:     #aa0000;
@lht-red:          #dd3300; // Traffic light status
@lht-orange:       #ff6600;
@lht-blue:         #3050cc;
@lht-medium-blue:  #5588ff;
@lht-light-blue:   #ccdbff; // Secondary selections / mouse over for table row
@lht-dark-green:   #226600;
@lht-medium-green: #339933; // Traffic light status
@lht-light-green:  #77cc66;

@lht-light-red-alert:     #f9d5cc;
@lht-light-yellow-alert:  #fff0cc;
@lht-light-green-alert:   #d4dfcb;
@lht-light-blue-alert:    @lht-light-blue;


// Experimental: Try a more compact layout
// @see also lht-forms.less lht-buttons.less
// -----------------------------------------

@padding-base-vertical:     3px;
@padding-base-horizontal:   6px;

@padding-large-vertical:    6px;
@padding-large-horizontal:  12px;

@padding-small-vertical:    @padding-base-vertical;
@padding-small-horizontal:  @padding-base-horizontal;

// custom (for use with .pagination-sm
@padding-medium-vertical:    4px;
@padding-medium-horizontal:  8px;


@form-group-margin-bottom:  4px;



// Bootstrap color mapping (override)
// -----------------------------------

@brand-primary: @lht-yellow;
@brand-info:    @lht-navy-blue;
@brand-success: @lht-medium-green;
@brand-warning: @lht-yellow;
@brand-danger:  @lht-red;

@gray-darker:  @lht-dark-gray-a;
@gray-dark:    @lht-dark-gray-b;
@gray:         @lht-medium-gray-a;
@gray-light:   @lht-light-gray-a;
@gray-lighter: @lht-light-gray-b;

@text-color:              @gray-darker;
@text-muted:              @lht-medium-gray-c;
@input-color:             @text-color;
@input-color-placeholder: @lht-medium-gray-c;
@input-bg-disabled:       #eeeeee;
@input-group-addon-bg:    @lht-light-gray-c;

@link-color:         @lht-navy-blue-even-lighter; // experimental!
@link-hover-color:   @lht-navy-blue;

@navbar-default-color:               white;
@navbar-default-bg:                  @brand-primary;
@navbar-default-brand-color:         @navbar-default-color;
@navbar-default-brand-hover-color:   @navbar-default-color;
@navbar-default-link-color:          @brand-info;
@navbar-default-link-hover-color:    white;
@navbar-default-link-active-color:   white;
@navbar-default-link-disabled-color: @lht-medium-gray-b;
@navbar-default-link-disabled-bg:    transparent;


@navbar-default-toggle-color:		 @lht-navy-blue;
@navbar-default-toggle-border-color: transparent;
@navbar-default-toggle-icon-bar-bg:  white;
@navbar-default-toggle-hover-bg:     darken(@lht-yellow, 5%);
@navbar-default-toggle-focus-bg:     transparent;

@dropdown-link-disabled-color: @lht-medium-gray-c;

// not really used - but anyway...

@navbar-inverse-color:      white;
@navbar-inverse-bg:         @brand-info;
@navbar-inverse-link-color: #fff;

@navbar-inverse-toggle-border-color: @navbar-default-toggle-border-color;
@navbar-inverse-toggle-icon-bar-bg:  @navbar-default-toggle-icon-bar-bg;
@navbar-inverse-toggle-hover-bg:     @lht-medium-gray-b;



//font-size
@font-size-base:          14px;
@font-size-large:         ceil((@font-size-base * 1.25)); // ~18px
@font-size-small:         ceil((@font-size-base * 0.85)); // ~12px

@font-size-h1:            ceil((@font-size-base * 1.7)); // ~24px
@font-size-h2:            ceil((@font-size-base * 1.5)); 
@font-size-h3:            @font-size-large;
@font-size-h4:            ceil((@font-size-base * 1.15)); // ~16px
@font-size-h5:            @font-size-base;
@font-size-h6:            ceil((@font-size-base * 0.85)); // ~12px

// LHT Specific components
// ------------------------------------------------

@lht-navbar-shadow: none; //inset 0 1px 0 rgba(255,255,255,.15), 0 1px 5px rgba(0,0,0,.075);
@navbar-default-text-shadow: none;// 0 1px 0 rgba(255,255,255,.25);
@lht-text-shadow-white-on-yellow: none;//-1px 0px 2px rgba(100, 100, 100, .2); //0 1px 2px rgba(100, 100, 100, .2);

@lht-titlebar-gradient-top:    @lht-yellow-lighter;
@lht-titlebar-gradient-bottom: @lht-yellow;

// Menubar default

@lht-menubar-gradient-top:    @lht-light-gray-c;
@lht-menubar-gradient-bottom: darken(@lht-light-gray-c, 5%); //@lht-light-gray-b;

@lht-menubar-separator:                 1px;
@lht-menubar-separator-color:           white;
@lht-menubar-outline-horizontal:        0px;
@lht-menubar-outline-horizontal-color:  transparent;
@lht-menubar-outline-vertical:          0px;
@lht-menubar-outline-vertical-color:    transparent;

// Menubar collapsed

@lht-menubar-collapsed-gradient-top:          darken(@lht-light-gray-c, 3%);
@lht-menubar-collapsed-gradient-bottom:       darken(@lht-light-gray-c, 3%);
@lht-menubar-collapsed-right-gradient-top:    white; //@lht-titlebar-gradient-top;
@lht-menubar-collapsed-right-gradient-bottom: white; //@lht-titlebar-gradient-bottom;
@lht-menubar-collapsed-right-inset:           0 9px 12px -10px @lht-medium-gray-a inset; // none;

@lht-menubar-collapsed-separator:       @lht-menubar-separator;
@lht-menubar-collapsed-separator-color: @lht-menubar-separator-color;

// Menu items default behavior

@lht-menu-text-color:             @lht-dark-gray-a;
@lht-menu-text-shadow:            @navbar-default-text-shadow;

@lht-menu-hover-text-color:       @lht-menu-text-color;
@lht-menu-hover-text-shadow:      @lht-menu-text-shadow;
@lht-menu-hover-gradient-top:     @lht-light-gray-b;
@lht-menu-hover-gradient-bottom:  @lht-light-gray-b;

@lht-menu-active-text-color:      white;
@lht-menu-active-text-shadow:     @lht-menu-text-shadow;
@lht-menu-active-gradient-top:    @lht-navy-blue;
@lht-menu-active-gradient-bottom: @lht-navy-blue;

@lht-menu-open-text-color:        white;
@lht-menu-open-text-shadow:       @lht-menu-text-shadow;
@lht-menu-open-gradient-top:      @lht-navy-blue;
@lht-menu-open-gradient-bottom:   @lht-navy-blue;

// Menu items in collapsed mode behavior

@lht-menu-collapsed-text-color:             @lht-menu-text-color;
@lht-menu-collapsed-text-shadow:            @lht-menu-text-shadow;

@lht-menu-collapsed-hover-text-color:       @lht-menu-text-color; // @lht-menu-hover-text-color;
@lht-menu-collapsed-hover-text-shadow:     	@lht-menu-text-shadow; // @lht-menu-hover-text-shadow;
@lht-menu-collapsed-hover-gradient-top:     @lht-light-gray-b; // @lht-menu-hover-gradient-top;
@lht-menu-collapsed-hover-gradient-bottom:  @lht-light-gray-b; // @lht-menu-hover-gradient-bottom;

@lht-menu-collapsed-active-text-color:      @lht-menu-active-text-color;
@lht-menu-collapsed-active-text-shadow:     @lht-menu-active-text-shadow;
@lht-menu-collapsed-active-gradient-top:    @lht-menu-active-gradient-top;
@lht-menu-collapsed-active-gradient-bottom: @lht-menu-active-gradient-bottom;

@lht-menu-collapsed-open-text-color:        @lht-menu-open-text-color;
@lht-menu-collapsed-open-text-shadow:       @lht-menu-open-text-shadow;
@lht-menu-collapsed-open-gradient-top:      @lht-menu-open-gradient-top;
@lht-menu-collapsed-open-gradient-bottom:   @lht-menu-open-gradient-bottom;

// right meta-navigation -> at the bottom

@lht-menu-collapsed-right-text-color:             @lht-navy-blue;
@lht-menu-collapsed-right-text-shadow:            @lht-menu-text-shadow;

@lht-menu-collapsed-right-hover-text-color:       @lht-navy-blue; // @lht-menu-hover-text-color;
@lht-menu-collapsed-right-hover-text-shadow:      @navbar-default-text-shadow; // @lht-menu-hover-text-shadow;
@lht-menu-collapsed-right-hover-gradient-top:     @lht-light-gray-b; // @lht-menu-hover-gradient-top;
@lht-menu-collapsed-right-hover-gradient-bottom:  @lht-light-gray-b; //@lht-menu-hover-gradient-bottom;

@lht-menu-collapsed-right-active-text-color:      @lht-navy-blue; // @lht-menu-active-text-color;
@lht-menu-collapsed-right-active-text-shadow:     @navbar-default-text-shadow; // @lht-menu-active-text-shadow;
@lht-menu-collapsed-right-active-gradient-top:    white; // @lht-menu-active-gradient-top;
@lht-menu-collapsed-right-active-gradient-bottom: white; // @lht-menu-active-gradient-bottom;

@lht-menu-collapsed-right-open-text-color:        @lht-navy-blue; // @lht-menu-open-text-color;
@lht-menu-collapsed-right-open-text-shadow:       @navbar-default-text-shadow; // @lht-menu-open-text-shadow;
@lht-menu-collapsed-right-open-gradient-top:      white; // @lht-menu-open-gradient-top;
@lht-menu-collapsed-right-open-gradient-bottom:   white; // @lht-menu-open-gradient-bottom;


// Dropdowns default behaviour

@lht-dropdown-hover-text-color:       white;
@lht-dropdown-hover-gradient-top:     @lht-navy-blue;
@lht-dropdown-hover-gradient-bottom:  @lht-navy-blue;

@lht-dropdown-active-text-color:      white;
@lht-dropdown-active-gradient-top:    @lht-navy-blue;
@lht-dropdown-active-gradient-bottom: @lht-navy-blue;


// Specific mixins
.lht-menubar-separator {
  border-right: @lht-menubar-separator solid;
  border-color: @lht-menubar-separator-color !important;
}

.lht-menubar-outline {
  border-top: @lht-menubar-outline-horizontal solid;
  border-top-color: @lht-menubar-outline-horizontal-color !important;
  border-bottom: @lht-menubar-outline-horizontal solid;
  border-bottom-color: @lht-menubar-outline-horizontal-color !important;
  border-left: @lht-menubar-outline-vertical solid;
  border-left-color: @lht-menubar-outline-vertical-color !important;
  border-right: @lht-menubar-outline-vertical solid;
  border-right-color: @lht-menubar-outline-vertical-color !important;
}

.lht-menubar-collapsed-separator {
  border-top: @lht-menubar-collapsed-separator solid;
  border-color: @lht-menubar-collapsed-separator-color !important;
}

// Buttons
// ------------------------------------------------

// Bootstrap standards
@btn-default-color:              @lht-menu-text-color;
@btn-default-bg:                 @lht-menubar-gradient-top;
@btn-default-border:             darken(@btn-default-bg, 5%);

@btn-primary-color:              @lht-menu-active-text-color;
@btn-primary-bg:                 @lht-navy-blue;
@btn-primary-border:             darken(@btn-primary-bg, 5%);

@btn-info-bg: @lht-medium-blue; // not used, but anyway...

// LHT specific
@lht-btn-default-text-color:      @lht-menu-text-color;
@lht-btn-default-text-shadow:     @lht-menu-text-shadow;
@lht-btn-default-gradient-top:    @lht-menubar-gradient-top;
@lht-btn-default-gradient-bottom: @lht-menubar-gradient-bottom;

@lht-btn-default-hover-gradient-top:     @lht-menu-hover-gradient-top;
@lht-btn-default-hover-gradient-bottom:  @lht-menu-hover-gradient-bottom;
@lht-btn-default-focus-gradient-top:     darken(@lht-btn-default-hover-gradient-bottom, 3%); // turn around!
@lht-btn-default-focus-gradient-bottom:  darken(@lht-btn-default-hover-gradient-top, 3%);
@lht-btn-default-disabled-bg-color:      @lht-btn-default-gradient-top;
@lht-btn-default-disabled-text-color:    @navbar-default-link-disabled-color;

@lht-btn-primary-text-color:      @lht-menu-active-text-color;
@lht-btn-primary-text-shadow:     @lht-menu-active-text-shadow;
@lht-btn-primary-gradient-top:    @lht-navy-blue-lighter;
@lht-btn-primary-gradient-bottom: @lht-navy-blue-darker;

@lht-btn-primary-hover-gradient-top:    @btn-primary-bg;
@lht-btn-primary-hover-gradient-bottom: @btn-primary-bg;
@lht-btn-primary-focus-gradient-top:    @lht-navy-blue-darker;
@lht-btn-primary-focus-gradient-bottom: @lht-navy-blue-darker;
@lht-btn-primary-disabled-bg-color:     @lht-medium-gray-c;
@lht-btn-primary-disabled-text-color:   @lht-btn-primary-text-color;

@btn-link-disabled-color: @lht-btn-default-disabled-text-color;


// Typography
// ------------------------------------------------

@lht-heading-color: @lht-navy-blue;
@lht-subhead-color: @lht-medium-gray-b;


// Tables
// ------------------------------------------------

@table-bg-accent:       @lht-light-gray-c;
@table-border-color:    @panel-default-border;  // change with panels...
@table-bg-hover:        @lht-light-blue; // Think about!
@table-bg-active:       @lht-navy-blue;
@table-color-active:    @lht-navy-blue;

// Navs (Tabs & Pills)
// ------------------------------------------------

// Shared...
@nav-link-padding:                          7px 15px;
@nav-link-hover-bg:                         @lht-menu-hover-gradient-bottom;

@nav-disabled-link-color:                   @navbar-default-link-disabled-color;
@nav-disabled-link-hover-color:             @navbar-default-link-disabled-color;

// Tabs
@nav-tabs-border-color:                     @panel-default-border;
@nav-tabs-link-hover-border-color:          @panel-default-border;

@nav-tabs-active-link-hover-color:          white;
@nav-tabs-active-link-hover-bg:             @lht-navy-blue;
@nav-tabs-active-link-hover-border-color:   @lht-navy-blue;

@nav-tabs-justified-link-border-color:            #ddd;
@nav-tabs-justified-active-link-border-color:     @body-bg;

// custom additions:
@lht-nav-tabs-bg-color: white;
@lht-tab-pane-bg-color: #F5F5F5;
@lht-nav-tabs-bg-color-active: @lht-tab-pane-bg-color; 
@lht-nav-tab-color: @lht-navy-blue;

// Pills
@nav-pills-active-link-hover-color:         white;
@nav-pills-active-link-hover-bg:            @lht-navy-blue;


// Panels
// ------------------------------------------------
@panel-heading-padding:       7px 15px;

@panel-default-text:          @text-color;
@panel-default-heading-bg:    @lht-menubar-gradient-top; // just as fallback

@panel-primary-text:          white;
@panel-primary-border:        @lht-navy-blue;
@panel-primary-heading-bg:    @lht-navy-blue;

@panel-footer-bg:             @lht-menubar-gradient-top;

@lht-panel-heading-min-height: 36px;


// Modals
// ---------------------------------------------------
@lht-modal-title-color:        @panel-primary-text;
@lht-modal-title-padding:      8px 15px 6px 15px;
@lht-modal-heading-bg:         @panel-primary-heading-bg;
@lht-modal-heading-min-height: @lht-panel-heading-min-height;

@modal-title-line-height:      @headings-line-height;
@modal-content-border-color:   transparent;
@modal-content-fallback-border-color: transparent;

// Wells
// ------------------------------------------------

@well-bg:     #eeeeee;
@well-border: transparent;


// Tooltips
// ------------------------------------------------

@tooltip-max-width:           280px;
@tooltip-color:               white;
@tooltip-bg:                  @lht-dark-gray-a;
@tooltip-opacity:             0.85;


// Popovers
// ---------------------------------------------------

@popover-bg:                          #fff;
@popover-max-width:                   276px;
@popover-border-color:                rgba(0,0,0,.2);
@popover-fallback-border-color:       #ccc;

@popover-title-bg:                    @lht-menubar-gradient-top; // just as fallback


/// Badges
// ----------------------------------------------------

@badge-color: white;
@badge-bg:    @lht-medium-gray-a;

@badge-active-color:          @lht-navy-blue;
@badge-active-bg:             white;

@badge-link-hover-color:      white;

// custom additions:
@lht-badge-link-hover-bg:       @lht-navy-blue;
@lht-badge-bg-primary:          @lht-navy-blue;
@lht-badge-bg-disabled:         @lht-medium-gray-c;
@lht-badge-bg-primary-disabled: rgba(150, 150, 160, 0.85); // disabled badge on @lht-navy-blue background

// special behaviour mixin:
.lht-active-badge-link-hover {
  color: @badge-color;
  background-color: @lht-badge-link-hover-bg;
  @shadow-border: inset 0px 0px 1px 2px @badge-color;
  .box-shadow(@shadow-border);
}


// Custom toolbar icons
// --------------------------------------------------------

@lht-toolbar-link-color:                   #6C6C6C; // custom!
@lht-toolbar-link-hover-color:             @link-hover-color;
@lht-toolbar-link-disabled-color:          #AAAAAA; // custom!
@lht-toolbar-link-primary-disabled-color:  @lht-badge-bg-primary-disabled; // disabled primary panel heading link

// Pagination
// ---------------------------------------------------------

@pagination-color:                     @text-color;
@pagination-bg:                        #fff;
@pagination-border:                    #ddd;

@pagination-hover-color:               @link-hover-color;
@pagination-hover-bg:                  @nav-link-hover-bg;
@pagination-hover-border:              @pagination-border;

@pagination-active-color:              #fff;
@pagination-active-bg:                 @lht-navy-blue;
@pagination-active-border:             @lht-navy-blue;

@pagination-disabled-color:            @dropdown-link-disabled-color;
@pagination-disabled-bg:               #fff;
@pagination-disabled-border:           @pagination-border;


// Code
// ----------------------------------

@code-color:  #c7254e;  // standard bootstrap
@code-bg:     #f9f2f4;  // standard bootstrap

@kbd-color:   #fff;
@kbd-bg:      @lht-dark-gray-b; // @lht-medium-gray-a;



// Forms states and alerts
// -----------------------------------

@state-success-text:             @text-color;
@state-success-bg:               @lht-light-green-alert;
@state-success-border:           @lht-medium-green;

@state-info-text:                @text-color;
@state-info-bg:                  @lht-light-blue-alert;
@state-info-border:              @lht-yellow;

@state-warning-text:             @text-color;
@state-warning-bg:               @lht-light-yellow-alert;
@state-warning-border:           @lht-yellow;

@state-danger-text:              @text-color;
@state-danger-bg:                @lht-light-red-alert;
@state-danger-border:            @lht-red;
@state-danger-text-placeholder:  #db8e89;


@alert-success-bg:            @state-success-bg;
@alert-success-text:          @state-success-text;
@alert-success-border:        transparent;

@alert-info-bg:               @state-info-bg;
@alert-info-text:             @state-info-text;
@alert-info-border:           transparent;

@alert-warning-bg:            @state-warning-bg;
@alert-warning-text:          #5d4c05;;
@alert-warning-border:        transparent;

@alert-danger-bg:             @state-danger-bg;
@alert-danger-text:           #88141a;
@alert-danger-border:         transparent;










