@import "../variables.less";
@import "../mixins.less";
@import "lht-variables.less";

.table {

  // Make all our tables verically condensed by default
  .table-condensed-vertically;

  // Outer (rounded) border by default
  border: 1px solid @table-border-color;
  border-radius: @panel-border-radius;
  border-collapse: separate;

  // Table header (tables.less:41)
  > thead > tr > th {
    // Same gradient as menu bar
    #gradient > .vertical (@start-color: @lht-menubar-gradient-top; @end-color: @lht-menubar-gradient-bottom);
    border: none;

    + th {
      // Again same separator as menu bar
      border-left: @lht-menubar-separator solid;
      border-left-color: @lht-menubar-separator-color;
    }

    &.sortable {
      .table-header-sortable;
    }
  }
  > thead.sortable > tr > th {
    .table-header-sortable;
  }
}

.table-striped {
  > tbody > tr {

    // Zebras need nor borders ;-)
    > th,
    > td {
      border-color: transparent;
    }

    // Since our table headers have a background we need swap the zebra-stripe style...
    // (overrides tables.less:113)

    &:nth-of-type(odd) {
      background-color: @table-bg;
    }
    &:nth-of-type(even) {
      background-color: @table-bg-accent;
    }
  }
}

> thead,
> tbody,
> tfoot {
  > tr {
    > th,
    > td {
      padding: @table-cell-padding;
      line-height: @line-height-base;
      vertical-align: top;
      border-top: 1px solid @table-border-color;
    }
  }
}

// tables.less:120
// ---------------
// Hover effect just copied here since it has to come after the potential zebra striping

.table-hover {
  > tbody > tr:hover {
    background-color: @table-bg-hover;
  }
}

// table-row.less
// --------------
// set specific text color for active (navy blue background)

.table > thead > tr,
.table > tbody > tr,
.table > tfoot > tr {
  > td.active,
  > th.active,
  &.active > td,
  &.active > th {
    color: @lht-menu-active-text-color;
  }
}

// ...and don't darken the color for hover on active
.table-hover > tbody > tr {
  > td.active:hover,
  > th.active:hover,
  &.active:hover > td,
  &:hover > .active,
  &.active:hover > th {
    background-color: @table-bg-active;
  }
}


.table-condensed-vertically {
  > thead,
  > tbody,
  > tfoot {
    > tr {
      > th,
      > td {
        padding-top: @table-condensed-cell-padding;
        padding-bottom: @table-condensed-cell-padding;

      }
    }
  }
}

//
// Adjustments to bootstraps default .table-responsive (tables.less:171)
// ---------------------------------------------------------------------

.table-responsive {

  @media screen and (max-width: @screen-xs-max) {

    // we have our custom border style...
    border-radius: @panel-border-radius;

    // ...and need to remove duplicated borders
    > .table {
      border: none;
    }
  }
}



//
// Responsive (dissolved) table based on "no more tables" approach
// ---------------------------------------------------------------

@media only screen and (max-width: @screen-xs-max) {

  /* Force table to not be like tables anymore */
  .table-dissolve table,
  .table-dissolve thead,
  .table-dissolve tbody,
  .table-dissolve th,
  .table-dissolve td,
  .table-dissolve tr {
    display: block;
  }

  /* Hide table headers (but not display: none;, for accessibility) */
  .table-dissolve thead tr {
    position: absolute;
    top: -9999px;
    left: -9999px;
  }

  /* Add a separator line between rows */
  .table-dissolve tr {
    border-bottom: 1px solid @table-border-color;
    &:last-child {
      border-bottom-color: transparent;
    }
  }

  @label-column-width-sm: 45%;

  /* Make each cell behave  like a "row" */
  .table-dissolve td {
    border-bottom: 1px solid transparent; // just to ensure exact spacing
    position: relative;
    white-space: normal;
    text-align:left;
    padding-left: calc(@label-column-width-sm + 12px) !important;
  }

  /* Label the data rows */
  .table-dissolve {
    & td:before,
    & th:before {
      content: attr(data-title);
    }
  }

  /* Place the labels */
  .table-dissolve:not(.row-header) td:before {
    .table-dissolve-cell-label;
  }

  /* Special: use the first column values as header */
  .table-dissolve.row-header tbody > tr {

    &:not(:hover) {
      background-color: transparent;
    }

    & > th:first-child,
    & > td:first-child {

      border-bottom: 1px solid @table-border-color !important;
      text-align: left !important;
      color: @text-color; // needed to override possibly active text color
      font-weight: bold;
      padding-left: @table-cell-padding !important;
      #gradient > .vertical (@start-color: @lht-menubar-gradient-top; @end-color: @lht-menubar-gradient-bottom);
      background-color: @lht-menubar-gradient-top; // to avoid that nasty gap
    }

    & > th:not(:first-child),
    & > td:not(:first-child) {

      &:before {
        .table-dissolve-cell-label;
      }
    }

  }

  // Mixin to position the labels
  .table-dissolve-cell-label() {
    position: absolute;
    top: @table-condensed-cell-padding; // top/left values mimic padding
    left: @table-cell-padding;
    width: @label-column-width-sm;
    padding-right: 10px;
    white-space: nowrap;
    text-align: right;
    font-weight: bold;
    content: attr(data-title);
  }

}

// Hack: Make the dissolved label column a bit smaller for xs screens...

@media only screen and (max-width: @screen-xs) {

  @label-column-width-xs: 40%;

  .table-dissolve td {
    padding-left: calc(@label-column-width-xs + 12px) !important;
  }

  .table-dissolve:not(.row-header) td:before {
    width: @label-column-width-xs;
  }

  .table-dissolve.row-header tbody > tr {
    & > th:not(:first-child),
    & > td:not(:first-child) {
      &:before {
        width: @label-column-width-xs;
      }
    }
  }
}

th.sorted-asc {
  background-image: url('../images/sorted-asc.png') !important;
  background-size: 2em 2em;
  background-repeat: no-repeat !important;
  background-position: right center;
  background-color: @lht-menu-hover-gradient-bottom; //@lht-light-gray-b;
}

th.sorted-desc {
  background-image: url('../images/sorted-desc.png') !important;
  background-size: 2em 2em;
  background-repeat: no-repeat !important;
  background-position: right center;
  background-color: @lht-menu-hover-gradient-bottom; //@lht-light-gray-b;
}

// mixin
// TODO: refactor to a more general way #gradient > .vertical-with image
.table-header-sortable(@image: url('../images/sortable.png'), @start-color: @lht-menubar-gradient-top, @end-color: @lht-menubar-gradient-bottom, @start-percent: 0%, @end-percent: 100%) {
  background-image: @image, -webkit-linear-gradient(top, @start-color @start-percent, @end-color @end-percent); // Safari 5.1-6, Chrome 10+
  background-image: @image, -o-linear-gradient(top, @start-color @start-percent, @end-color @end-percent); // Opera 12
  background-image: @image, linear-gradient(to bottom, @start-color @start-percent, @end-color @end-percent); // Standard, IE10, Firefox 16+, Opera 12.10+, Safari 7+, Chrome 26+
  background-size: 2em 2em, auto;
  background-repeat: no-repeat, repeat-x;
  background-position: right center, 0;
  //filter: e(%("progid:DXImageTransform.Microsoft.gradient(startColorstr='%d', endColorstr='%d', GradientType=0)", argb(@start-color), argb(@end-color))); // IE9 and down
}




