@import "../variables.less";
@import "../mixins.less";
@import "lht-variables.less";

// Adapted from theme.less, 18 ff.
// --------------------------------------------------

// Common styles
.btn-default {
  color: @lht-btn-default-text-color;
  text-shadow: @lht-btn-default-text-shadow;
}

.btn-primary {
  color: @lht-btn-primary-text-color;
  text-shadow: @lht-btn-primary-text-shadow;
}

// reset default styles...
.btn-default,
.btn-primary {

//  background-color: transparent;
  .box-shadow(none);

  &:active,
  &.active,
  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    .box-shadow(none);
  }

  .badge {
    text-shadow: none;
  }
}

// Overwrite base styles from buttons.less
// --------------------------------------------------

// since we have made the layout more compact by reducing the base paddings let's give the buttons their original size again...

.btn {
  .button-size((@padding-base-vertical * 2); (@padding-base-horizontal * 2); @font-size-base; @line-height-base; @btn-border-radius-base);

  // we use our own particular colors instead of opacity
  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    .opacity(1);
  }
}

.btn-lg {
  .button-size((@padding-large-vertical + 4); (@padding-large-horizontal + 4); @font-size-large; @line-height-large; @btn-border-radius-large);
}

.btn-sm {
  .button-size((@padding-small-vertical + 1); (@padding-small-horizontal + 4); @font-size-small; @line-height-small; @btn-border-radius-small);
}

.btn-xs {
  .button-size(@padding-xs-vertical; @padding-xs-horizontal; @font-size-small; @line-height-small; @btn-border-radius-small);
}

// make sure we use our special sizes also for button groups
.btn-group-xs > .btn { &:extend(.lht-theme .btn-xs);
}

.btn-group-sm > .btn { &:extend(.lht-theme .btn-sm);
}

.btn-group-lg > .btn { &:extend(.lht-theme .btn-lg);
}

// Mixin for generating LHT styles (adapted from theme.less, 50 ff.)
// -----------------------------------------------------------------

.lht-btn-styles(
  @gradient-top:          @lht-btn-default-gradient-top,
  @gradient-bottom:       @lht-btn-default-gradient-bottom,
  @hover-gradient-top:    @lht-btn-default-hover-gradient-top,
  @hover-gradient-bottom: @lht-btn-default-hover-gradient-bottom,
  @focus-gradient-top:    @lht-btn-default-focus-gradient-top,
  @focus-gradient-bottom: @lht-btn-default-focus-gradient-bottom,
  @disabled-bg-color:     @lht-btn-default-disabled-bg-color,
  @disabled-text-color:   @lht-btn-default-disabled-text-color) {

  #gradient > .vertical(@start-color: @gradient-top; @end-color: @gradient-bottom);
  .reset-filter(); // Disable gradients for IE9 because filter bleeds through rounded corners; see https://github.com/twbs/bootstrap/issues/10620
  //background-repeat: repeat-x;
  border-color: darken(@gradient-bottom, 10%);
  // reset defaults:
  background-position: 0;
//  background-color: transparent;

  &:hover {
    #gradient > .vertical(@start-color: @hover-gradient-top; @end-color: @hover-gradient-bottom);
    // reset defaults:
    background-position: 0;
//    background-color: transparent;
  }

  &:focus,
  &:active,
  &.active {
    #gradient > .vertical(@start-color: @focus-gradient-top; @end-color: @focus-gradient-bottom);
    border-color: darken(@focus-gradient-bottom, 10%);
    // reset defaults:
    background-position: 0;
//    background-color: transparent;
  }

  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    &,
    &:hover,
    &:focus,
    &.focus,
    &:active,
    &.active {
      color: @disabled-text-color;
      background-color: @disabled-bg-color;
      border-color: darken(@disabled-bg-color, 6%);
      background-image: none;
    }
  }
}

// Apply the mixin to the buttons
.btn-default {
  .lht-btn-styles;
}

.btn-primary {
  .lht-btn-styles(
    @lht-btn-primary-gradient-top,
    @lht-btn-primary-gradient-bottom,
    @lht-btn-primary-hover-gradient-top,
    @lht-btn-primary-hover-gradient-bottom,
    @lht-btn-primary-focus-gradient-top,
    @lht-btn-primary-focus-gradient-bottom,
    @lht-btn-primary-disabled-bg-color,
    @lht-btn-primary-disabled-text-color);
}

.btn-link {
  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    &,
    &:hover,
    &:focus,
    &.focus,
    &:active,
    &.active {
      color: @btn-link-disabled-color;
    }
  }
}

