@import "../variables.less";
@import "../mixins.less";
@import "lht-variables.less";

.well {
  min-height: 20px;
  padding: 15px;
  margin-bottom: 20px;
  background-color: @well-bg;
  border: 1px solid @well-border;
  border-color: @well-border;
  border-radius: @border-radius-base;
  .box-shadow(none);
  background-image: none;
}



