@import "../variables.less";
@import "../mixins.less";
@import "lht-variables.less";

h1, h2, h3, h4,
.h1, .h2, .h3, .h4 {
  color: @lht-heading-color;
}

h5, h6,
.h5, .h6 {
  color: @lht-subhead-color;
}

.lht-page-title {
	 @media (max-width: @grid-float-breakpoint-max) {
   		position: absolute;
    	font-size: 11pt;
    	top: 15px;
    	left: 20%;
    	z-index: 1031; //more z then navbar
    	
    	text-align: center;
    	//leave space on right side for toggle-button
    	width: 60%;
    	
    	
    	
		&.lht-page-title-fixed-top{
			position: fixed;
		}
  	}
  	
}
