@import "../variables.less";
@import "../mixins.less";
@import "lht-variables.less";

//
// Badges
// --------------------------------------------------


// Base class
.badge {

  // do some decent positioning:
  .btn &,
  .nav-tabs > li & {
    margin-left: 5px;
    margin-right: -2px;
  }

  .dropdown-menu > li & {
    margin-left: 10px;
    margin-right: -5px;
    margin-top: -2px;
    &.pull-right {
      margin-top: 1px;
    }

    // Note on right aligned badges in menus:
    //
    // float: right
    // .pull-right
    //
    // does only work as long as the menu item + badge is not longer than the min-width of 160px
    // for longer menu items the longest one determines the width and must not use .pull-right :-(

  }
  .toolbar &,
  .panel-heading & {
    margin-top: -2px;
  }


  &, .btn-default & {
    background-color: @badge-bg;
  }

  //additional LHT variants
  &.badge-primary {
    background-color: @lht-badge-bg-primary;
  }

  li.active & {
    // just negative
    color: @badge-active-color;
    background-color: @badge-active-bg;
  }

  &.badge-disabled,
  &.disabled,
  .disabled & {
    background-color: @lht-badge-bg-disabled;
  }

  .btn-primary.disabled & {
    // just negative
    color: @lht-btn-primary-disabled-bg-color;
    background-color: @lht-btn-primary-disabled-text-color;
  }


  // Hover state, but only for links (badges.less:39)
  a& {
    &:hover,
    &:focus {
      background-color: @lht-badge-link-hover-bg !important;
    }
  }

}
