@import "../variables.less";
@import "../mixins.less";
@import "lht-variables.less";

// navs.less:50
// ------------
.nav {
  // Open dropdowns: adjust the border color
  .open > a {
    &,
    &:hover,
    &:focus {
      color: @nav-tabs-active-link-hover-color;
      background-color: @nav-tabs-active-link-hover-bg;
      border-color: @nav-tabs-active-link-hover-border-color;
    }
  }
}

// navs.less:75
// ------------
.nav-tabs {
  border-bottom: 1px solid @nav-tabs-border-color;
  > li {
    // Actual tabs (as links)
    > a {
      color: @text-color;
      border: 1px solid @panel-default-border;
     
    }

    &:last-child > a {
      margin-right: 0;
    }

    &.active > a {
      &,
      &:hover,
      &:focus {
      	background-color: @lht-nav-tabs-bg-color-active;
      	color: @lht-nav-tab-color;
      	border-color: @panel-default-border;
    	border-bottom-color: transparent; 
        // make active tabs come to the front again
        z-index: 2;
      }
    }

  }
}

// navs.less:223
// -------------
.tab-content {
  margin-bottom: 20px;
  > .tab-pane {
    padding: 15px;
    border-left: 1px solid @panel-default-border;
    border-bottom: 1px solid @panel-default-border;
    border-right: 1px solid @panel-default-border;
    border-bottom-right-radius: @panel-border-radius;
    border-bottom-left-radius: @panel-border-radius;

  //  .box-shadow(0 1px 2px rgba(0, 0, 0, .05));
    // make it opaque and cover the shadow of the tabs
    background-color: @lht-tab-pane-bg-color;
    position: relative;
    z-index: 1;

  }
}

// navs.less:122
// -------------
.nav-pills > li {
  > a {
    color: @text-color;
    background-color: @lht-menubar-gradient-top;
    margin-right: 2px;
  }
  // keep the same backgroud for hover on disabled state
  &.disabled > a {
    &:hover,
    &:focus {
      background-color: @lht-menubar-gradient-top;
    }
  }
}

