@import "../variables.less";
@import "../mixins.less";
@import "lht-variables.less";

// use as mixin:
@import "lht-wells.less";

.toolbar {
  .well(); // mixin!
  padding-top: 7px;
  padding-bottom: 7px;
  margin-bottom: 10px;
}

.toolbar,
.panel-heading,
.panel-footer {

  min-height: @lht-panel-heading-min-height;

  & .dropdown:last-child a.dropdown-toggle {
    margin-right: 0;
  }

  & a:not(.badge) {
    color: @lht-toolbar-link-color;
    margin-right: 12px;

    &:last-child {
      margin-right: 0;
    }

    &:hover,
    &:focus {
      color: @lht-toolbar-link-hover-color;
      text-decoration: none;
      & > .badge {
        background-color: @lht-badge-link-hover-bg;
      }
    }

    &.disabled,
    &.disabled:hover,
    &.disabled:focus {
      color: @lht-toolbar-link-disabled-color;
      cursor: @cursor-disabled;
      & > .badge {
        background-color: @lht-badge-bg-disabled;
      }
    }
  }
}

//.panel-primary > .panel-heading .dropdown-menu a:not(.badge) {
//  color: red;
//}


.panel-primary > .panel-heading  {

  & a:not(.badge) {
    color: @panel-primary-text;

    &:hover,
    &:focus {
      color: @panel-primary-text;
      & > .badge {
        .lht-active-badge-link-hover; //same as "active": negative badge on navy-blue background
      }
    }

    &.disabled,
    &.disabled:hover,
    &.disabled:focus {
      color: @lht-toolbar-link-primary-disabled-color;
      cursor: @cursor-disabled;
      & > .badge {
        color: initial;
        background-color: @lht-badge-bg-primary-disabled;
        box-shadow: none;
      }
    }
  }

}



