@import "../variables.less";
@import "../mixins.less";
@import "lht-variables.less";

.navbar-default {
  border: 0 none !important;
}

.navbar-lht {
  border: 0 none !important;

  // let the navbar (in particular the non-floating menu bar) span the whole width
  .container, .container-fluid, .navbar-collapse {
    width: 100%;
    padding: 0;

    @media (max-width: @grid-float-breakpoint-max) {
      padding-right: 15px;
    }
  }

  // Copied from .navbar-default (theme.less, 133 ff.) with adjustments to gradients and shadow
  // ------------------------------------------------------------------------------------------

  #gradient > .vertical(@start-color: @lht-titlebar-gradient-top; @end-color: @lht-titlebar-gradient-bottom);
  .reset-filter(); // Remove gradient in IE<10 to fix bug where dropdowns don't get triggered
  //background-color: @lht-titlebar-gradient-bottom; //background causes a nasty yellow border in collapsed mode!
  background-size: 100% 4.55em; // don't let the gradient overlap our menu bar
  border-radius: @navbar-border-radius;
  .box-shadow(@lht-navbar-shadow);


  // Copied from .navbar-default (navbar.less, 395 ff.)
  // --------------------------------------------------

  .navbar-brand {
    color: @navbar-default-brand-color;
    text-shadow: @lht-text-shadow-white-on-yellow; // adjust shadow for white text on yellow bg
    &:hover,
    &:focus {
      color: @navbar-default-brand-hover-color;
      background-color: @navbar-default-brand-hover-bg;
    }
    	//Center Title if menu is collapsed
      @media (max-width: @grid-float-breakpoint-max) {
      		//remove existing margin and padding of parent emelemts
      	 	
    		margin: 0px;
    		padding: 0px;
    		
    		
        	width: 60%;
    		text-align: center;   
    		position: absolute;
    		left:20%;
    		top: 10px;
    		
    	}
    
  }

  .navbar-text {
    color: @navbar-default-color;
  }

  // Navbar defaults on yellow gradient (meta-navigation)
  .navbar-nav {
    > li > a {
      color: @navbar-default-link-color;

      &:hover,
      &:focus {
        color: @navbar-default-link-hover-color;
        background-color: @navbar-default-link-hover-bg;
      }
    }
    // ... we don't use any other active, disabled or open styles here
    > .open > a {
      &,
      &:hover,
      &:focus {
        color: @navbar-default-link-hover-color;
        background-color: @navbar-default-link-hover-bg;
      }
    }

    // and again customized: special special shadow for white text on yellow bg
    &.navbar-right {
      > li > a {
        &:hover,
        &:focus {
          text-shadow: @lht-text-shadow-white-on-yellow;
        }
      }
      > .open > a {
        &,
        &:hover,
        &:focus {
          text-shadow: @lht-text-shadow-white-on-yellow;
        }
      }

    }
  }

  .navbar-toggle {
    border-color: @navbar-default-toggle-border-color;
    color: @navbar-default-toggle-color;
    font-size: 35px;
    padding: 1px 6px;
    margin: 0px 15px;
    &:hover{
    	background-color: @navbar-default-toggle-hover-bg;
    }
    &:focus {
      background-color: @navbar-default-toggle-focus-bg;
    }
    .icon-bar {
      background-color: @navbar-default-toggle-icon-bar-bg;
    }
    &.navbar-back{
    	 @media (min-width: @grid-float-breakpoint) {
    	display:none;
    	}
    	float: none;
    	margin: 0px 5px;
    }
  }
  


  // ------------------------------------------------------------

  // place menubar below the title bar
  .navbar-header {
    float: none;
    height: 4.5em;
    padding-left: 1.5em;
    padding-top: 0.4em;
  }

  // padding for menu items in collapsed mode (for reuse)
  @padding-collapsed-vertical: 8px;
  @padding-collapsed-horizontal: 20px;

  // menubar defaults
  .navbar-collapse {

    // expanded
    @media (min-width: @grid-float-breakpoint) {

      #gradient > .vertical (@start-color: @lht-menubar-gradient-top; @end-color: @lht-menubar-gradient-bottom);
      .lht-menubar-outline;

      .navbar-nav > li > a {
        padding: 0.57em 1.818em;
      }

      .navbar-nav.navbar-right > li > a {
        padding-left: 0;
      }
    }

    // collapsed: using dropdown style as template
    @media (max-width: @grid-float-breakpoint-max) {

      padding: 0;
      margin-left: 0 !important; // override default ul
      width: auto;
      max-height: none !important;
      background-color: @dropdown-bg;
//      border-bottom: 1px solid @dropdown-fallback-border; // IE8 fallback
      border-bottom: 1px solid white;
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
      .box-shadow(0 6px 12px rgba(0, 0, 0, .175));
      background-clip: padding-box;

      .navbar-nav {
        margin: 0;
        > li {
          border: 0;
          .lht-menubar-collapsed-separator;
          > a {
            padding: @padding-collapsed-vertical @padding-collapsed-horizontal; // more compact like menubar in expanded mode
            //padding-left: 20px;
          }
        }
        .dropdown-toggle > span.caret{
        	float: right;
        	margin-top: 7px;
        }
        .open .dropdown-menu > li > a {
          padding-left: @padding-collapsed-horizontal; // 40px;
          //padding: 3px 20px 3px 40px; // more compact like dropdown default
        }
      }
    }
  }

  // Menubar items
  // --------------------------------------------

  .navbar-nav.navbar-menu {

    // menubar in expanded mode
    @media (min-width: @grid-float-breakpoint) {
      > li {
        .lht-menubar-separator;
      }
      .menubar-expanded-behavior;
    }

    // menubar in collapsed mode
    @media (max-width: @grid-float-breakpoint-max) {
      > li > a {
        #gradient > .vertical (@start-color: @lht-menubar-collapsed-gradient-top; @end-color: @lht-menubar-collapsed-gradient-bottom);
      }
      .menubar-collapsed-behavior;
    }
  }

  // meta-navigation in collapsed mode
  .navbar-nav.navbar-right {
    @media (max-width: @grid-float-breakpoint-max) {
      > li > a {
        #gradient > .vertical(@start-color: @lht-menubar-collapsed-right-gradient-top; @end-color: @lht-menubar-collapsed-right-gradient-bottom);
        padding-top: (@padding-collapsed-vertical + 2px); // some more px due to inset
      }
      // reset subsequent items
      > li + li > a {      
        padding-top: @padding-collapsed-vertical;
      }
      .menubar-collapsed-right-behavior;
    }
  }

  // meta-navigation in expanded mode
  .navbar-right {
    margin-top: (-3.5em - (@lht-menubar-outline-horizontal/@font-size-base));
    margin-right: 0;
    margin-left: -9999px; // Quick fix for 	trello issue: Fix overlapping right navigation and menubar links
    > li {
      border-right: 0;
      + li {
        padding-left: 0 !important;
      }
    }
  }

  // Additional space for the following containers when navbar-fixed-top
  &.navbar-fixed-top {
    + .container,
    + .container-fluid {
      margin-top: 115px;

      @media (max-width: @grid-float-breakpoint-max) {
        margin-top: 75px;
      }

    }
  }
}

// Undo rounded corners in static and fixed navbars
.navbar-static-top,
.navbar-fixed-top,
.navbar-fixed-bottom {
  border-radius: 0;
}

// Additional style for anchors when using a fixed navbar
// -----------------------------------------------------

a.navbar-fixed-top-offset {
  display: block;
  visibility: hidden;
  position: relative;
  top: -115px;
  @media (max-width: @grid-float-breakpoint-max) {
    position: relative;
    top: -75px;
  }

}





// Mixins for navbar menu behavior
// ----------------------------------------------

.menubar-expanded-behavior() {
  .menu-default-behavior;
  .menu-active-behavior;
  .menu-disabled-behavior;
  .menu-open-behavior;
}

.menubar-collapsed-behavior() {
  .menu-default-behavior(
    @lht-menu-collapsed-text-color,
    @lht-menu-collapsed-text-shadow,
    @lht-menu-collapsed-hover-text-color,
    @lht-menu-collapsed-hover-text-shadow,
    @lht-menu-collapsed-hover-gradient-top,
    @lht-menu-collapsed-hover-gradient-bottom
  );
  //.menu-active-behavior; // no extra decoration for active items (too colorful and confusing in collapsed mode!)
  .menu-collapsed-disabled-behavior; // different behavior in colloapsed mode: no extra decorations
  .menu-open-behavior(
    @lht-menu-collapsed-open-text-color,
    @lht-menu-collapsed-open-text-shadow,
    @lht-menu-collapsed-open-gradient-top,
    @lht-menu-collapsed-open-gradient-bottom);
}

.menubar-collapsed-right-behavior() {
  .menu-default-behavior(
    @lht-menu-collapsed-right-text-color,
    @lht-menu-collapsed-right-text-shadow,
    @lht-menu-collapsed-right-hover-text-color,
    @lht-menu-collapsed-right-hover-text-shadow,
    @lht-menu-collapsed-right-hover-gradient-top,
    @lht-menu-collapsed-right-hover-gradient-bottom
  );
  //.menu-active-behavior; // no extra decoration for active items (not applicable for meta-navigation!)
  .menu-disabled-behavior;
  .menu-open-behavior(
    @lht-menu-collapsed-right-open-text-color,
    @lht-menu-collapsed-right-open-text-shadow,
    @lht-menu-collapsed-right-open-gradient-top,
    @lht-menu-collapsed-right-open-gradient-bottom);
}

.menu-default-behavior(
  @text-color:            @lht-menu-text-color,
  @text-shadow:           @lht-menu-text-shadow,
  @hover-text-color:      @lht-menu-hover-text-color,
  @hover-text-shadow:     @lht-menu-hover-text-shadow,
  @hover-gradient-top:    @lht-menu-hover-gradient-top,
  @hover-gradient-bottom: @lht-menu-hover-gradient-bottom) {

  & > li > a {
    color: @text-color;
    text-shadow: @text-shadow !important;

    &:hover,
    &:focus {
      color: @hover-text-color;
      text-shadow: @hover-text-shadow !important;
      #gradient > .vertical (@start-color: @hover-gradient-top; @end-color: @hover-gradient-bottom);
    }
  }
}

.menu-active-behavior(
  @text-color:      @lht-menu-active-text-color,
  @text-shadow:     @lht-menu-active-text-shadow,
  @gradient-top:    @lht-menu-active-gradient-top,
  @gradient-bottom: @lht-menu-active-gradient-bottom) {

  & > .active > a {
    &,
    &:hover,
    &:focus {
      color: @text-color;
      text-shadow: @text-shadow !important;
      #gradient > .vertical (@start-color: @gradient-top; @end-color: @gradient-bottom);
    }
  }
}

.menu-disabled-behavior() {
  & > .disabled > a {
    &,
    &:hover,
    &:focus {
      color: @navbar-default-link-disabled-color;
      background-color: transparent;
      background-image: none; // Remove CSS gradient
    }
  }
}

.menu-collapsed-disabled-behavior() {
  & > .disabled > a {
    &,
    &:hover,
    &:focus {
      color: @navbar-default-link-disabled-color;
      // same background as all other collapsed menu items, no extra decoration
      #gradient > .vertical (@start-color: @lht-menubar-collapsed-gradient-top; @end-color: @lht-menubar-collapsed-gradient-bottom);
    }
  }
}

.menu-open-behavior(
  @text-color:      @lht-menu-open-text-color,
  @text-shadow:     @lht-menu-open-text-shadow,
  @gradient-top:    @lht-menu-open-gradient-top,
  @gradient-bottom: @lht-menu-open-gradient-bottom) {

  & > .open > a {
    &,
    &:hover,
    &:focus {
      color: @text-color;
      text-shadow: @text-shadow !important;
      #gradient > .vertical (@start-color: @gradient-top; @end-color: @gradient-bottom);
    }
  }
}







