@import "../variables.less";
@import "../mixins.less";
@import "lht-variables.less";

// Compact positioning

.alert {
  // default if above a section
  margin-bottom: 10px;

  // if below certain sections remove some of their space and add it after the alert again
  .well + &,
  .panel + &,
  .tab-content + &,
  .table + & {
    margin-top: -10px;
    margin-bottom: 20px;
  }
}

// Dismissible alerts close button
// (alerts.less, 42ff.)

.alert-dismissable, // The misspelled .alert-dismissable was deprecated in 3.2.0.
.alert-dismissible {
  .close {
    //color: inherit;
    .opacity(0.5)
  }
}

// override theme.less 213ff.

.alert-success {
  .lht-alert-variant(@alert-success-bg; @alert-success-text);
}

.alert-info {
  .lht-alert-variant(@alert-info-bg; @alert-info-text);
}

.alert-warning {
  .lht-alert-variant(@alert-warning-bg; @alert-warning-text);
}

.alert-danger {
  .lht-alert-variant(@alert-danger-bg; @alert-danger-text);
}

// custom mixin:

.lht-alert-variant(@background; @text-color) {
  background-color: @background;
  background-image: none; // remove gradient
  border: none;
  color: @text-color;

  hr {
    margin-top: 10px;
    margin-bottom: 10px;
    border: 0;
    //border-top: 1px solid darken(@background, 20%);
    border-top: 1px solid @text-color;
    .opacity(0.25);
    margin-right: -20px;
  }

  .alert-link {
    color: @text-color;
    &:hover {
      color: darken(@text-color, 10%);
    }
  }
}

