//
// Popovers
// --------------------------------------------------

.popover {

  // Additional margin to have some decent space at the right side of the screen
  //(unfortunately this does not work for the .left side)
  &.top,
  &.right,
  &.bottom {
    margin-right: 8px;
  }



  // remove that "shiny" edges...
  //padding: 0;

  // ... and adjust the color of the arrow for bottom aligned popovers
  // just in case there's a title...
  //&.bottom > .arrow {
  //  &:after {
  //    border-bottom-color: @popover-title-bg;
  //  }
  //}

}

.popover-title {
  padding: 7px 12px;  // back to 8px when "shiny" edges are removed!
  color: @text-color;
  // Same gradient as menu bar and tables
  #gradient > .vertical (@start-color: @lht-menubar-gradient-top; @end-color: @lht-menubar-gradient-bottom);
  background-color: @lht-menubar-gradient-top; // fallback
  border-bottom: none;
}

.popover-content {
  padding: 9px 12px;
}



