// ***************************************************
// More compact layout is achieved by
//
// @padding-base-vertical      6px -> 3px;
// @padding-base-horizontal:  12ox -> 6px;
//
// This file is to turn back unwanted side effects...
//
// ***************************************************

@import "../variables.less";
@import "../mixins.less";
@import "lht-variables.less";

.form-horizontal {
  // move labels closer to input fields
  .control-label {
    padding-right: 0px;
  }

  @media (min-width: @screen-xs-min) {
    .control-label {
      text-align: right;
      margin-bottom: 0;
      padding-top: (@padding-base-vertical + 1); // Default padding plus a border
    }
  }

  @media (max-width: @screen-xs-max) {
    .control-label {
      text-align: right;
      margin-bottom: 4px;
      padding-top: (@padding-base-vertical + 1); // Default padding plus a border
    }
  }

}

.form-control-static {
  //min-height: (@line-height-computed + @font-size-base);
  min-height: @input-height-base; // 28px;
}

// input-groups.less overrides
.input-group-addon {
  padding: @padding-base-vertical (@padding-base-horizontal * 2);
}

// Inside an input group make all buttons attached to an input small
.input-group-btn {
  & > .btn {
    //.lht-theme.btn-sm
    .btn-sm;
  }
}

// add additional margins to ensure proper spacing
.form-horizontal {
  .radio,
  .radio-inline,
  .checkbox,
  .checkbox-inline {
    &:last-child {
      margin-bottom: @form-group-margin-bottom;
    }
  }
}

.form-group:last-child {
  margin-bottom: 0;
}

select:not([multiple]) {

  // turn of browser specific renderig
  appearance: none; /* CSS3 */
  -webkit-appearance: none; /* Safari and Chrome */
  -moz-appearance: none; /* Firefox */
  -ms-appearance: none; /* Internet Explorer */
  -o-appearance: none; /* Opera */
  &::-ms-expand {
    visibility: hidden;
    //display: none;
  }

  // use custom caret
  background-image: url('../images/caret-select.png');
  background-size: 2em 2em;
  background-repeat: no-repeat;
  background-position: right center;

  // truncate text that may be too long
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  padding-right: 1.6em;
}

// Validation feedback states
// --------------------------

.has-success {
  .form-control-validation(@state-success-text; @state-success-border; @state-success-bg);
}
.has-warning {
  .form-control-validation(@state-warning-text; @state-warning-border; @state-warning-bg);
}
.has-error {
  .form-control-validation(@state-danger-text; @state-danger-border; @state-danger-bg);
  & .form-control {
    .placeholder(@state-danger-text-placeholder);
    //border-width: 2px;
  }

}

// Adjust Angular uib-select
// (angular-ui-select plugin)
// --------------------------

.ui-select-bootstrap .ui-select-toggle {
    background-color: white;
    background-image: none;
}

.ui-select-bootstrap .ui-select-choices-row > a:hover,
.ui-select-bootstrap .ui-select-choices-row > a:focus,
.ui-select-bootstrap .ui-select-choices-row.active > a  {
    background-color: @lht-navy-blue;
    color: white;
}
