@import "../variables.less";
@import "../mixins.less";
@import "lht-variables.less";

// !important since dropdowns should be the same everywhere (even in primary panel heading!)

.dropdown-menu > li:not(.disabled):not(.active) > a {

  color: @dropdown-link-color !important;

  // Touch me, baby, touch me...
  -webkit-tap-highlight-color: @lht-dropdown-hover-gradient-bottom;
  -webkit-touch-callout: none;

  // keep standard badges in any dropdown (even if the parent is active)
  & > .badge {
    color: @badge-color !important;
    background-color: @badge-bg !important;
  }

  &:hover,
  &:focus {

    // adapted fom theme.less (120 ff.):
    color: @lht-dropdown-hover-text-color !important;
    #gradient > .vertical(@start-color: @lht-dropdown-hover-gradient-top; @end-color: @lht-dropdown-hover-gradient-bottom);
    background-color: @lht-dropdown-hover-gradient-bottom;

    // hover on badges
    > .badge {
      color: @badge-active-color !important;
      background-color: @badge-active-bg !important;
    }
  }
}

// adapted from dropdowns.less (102 ff.)
.dropdown-menu > li.disabled > a {
  &,
  &:hover,
  &:focus {
    color: @dropdown-link-disabled-color !important;

    & > .badge {
      color: @badge-color !important;
      background-color: @lht-badge-bg-disabled !important;
      .box-shadow(none) !important;
    }
  }

  // Nuke hover/focus effects
  &:hover,
  &:focus {
    text-decoration: none;
    background-color: transparent;
    background-image: none; // Remove CSS gradient
    .reset-filter();
    cursor: @cursor-disabled;
  }
}


// adapted from theme.less (127 ff.):
.dropdown-menu > .active > a,
.dropdown-menu > .active > a:hover,
.dropdown-menu > .active > a:focus {
  color: @lht-dropdown-active-text-color !important;
  #gradient > .vertical(@start-color: @lht-dropdown-active-gradient-top; @end-color: @lht-dropdown-active-gradient-bottom);
  background-color: @lht-dropdown-active-gradient-bottom;
}

// special appearance for badges:
.dropdown-menu > li.active > a {
  & > .badge {
    color: @badge-active-color;
    background-color: @badge-active-bg;
  }
  &:hover,
  &:focus {
    & > .badge {
      .lht-active-badge-link-hover;
    }
  }
}




// Add some more carets...
// -------------------------------------------------

.caret-right {
  display: inline-block;
  width: 0;
  height: 0;
  margin-left: 2px;
  vertical-align: middle;
  border-left:   @caret-width-base dashed;
  border-left:   @caret-width-base solid ~"\9"; // IE8
  border-top:    @caret-width-base solid transparent;
  border-bottom: @caret-width-base solid transparent;
}

.caret-left {
  display: inline-block;
  width: 0;
  height: 0;
  margin-left: 2px;
  vertical-align: middle;
  border-right:  @caret-width-base dashed;
  border-right:  @caret-width-base solid ~"\9"; // IE8
  border-top:    @caret-width-base solid transparent;
  border-bottom: @caret-width-base solid transparent;
}

.caret-top {
  display: inline-block;
  width: 0;
  height: 0;
  margin-left: 2px;
  vertical-align: middle;
  border-bottom:  @caret-width-base dashed;
  border-bottom:  @caret-width-base solid ~"\9"; // IE8
  border-left:    @caret-width-base solid transparent;
  border-right:   @caret-width-base solid transparent;
}


